.arrow {
  position: absolute;
  top: -7px;
  right: 0px;

  border-top: 3px solid black;
  border-right: 3px solid black;
  padding: 4px;
  display: inline-block;
  transform: rotate(45deg);
}
.line {
  position: absolute;
  border-top: 3px solid black;
  transform-origin: left;
  z-index: 3;
}
